  padding-bottom: 30px;
}
.TOP {
  margin-top: 20px;
}
.text h4 {
  margin-top: 10px;
}
.text h1 {
  margin-top: 10px;
}
.home-linktoimg {
  margin-top: 30px;
  padding: 10px 30px;
  background: rgb(115, 191, 140);
  background: linear-gradient(
    222deg,
    rgba(115, 191, 140, 1) 50%,
