}
.Img12 {
  display: flex;
  width: 100vw;
  justify-content: center;
}

#message {
  margin: 10px;
  border-radius: 5px;
  outline: none;
  border: 1.5px solid black;
  width: 460px;
  height: 40px;
  text-align: center;
}
.search {
  /* margin: 100px; */
  margin-top: 15px;
  margin-left: 400px;
  margin-right: 0;
  position: absolute;
  z-index: 9706856;
  font-size: 30px;
}
.navbar-news {
  width: 100vw;
  /* position: absolute; */

  padding: 15px 0;
  text-align: center;
  background: rgb(131, 58, 180);
  background: linear-gradient(
    90deg,
    rgba(131, 58, 180, 1) 0%,
    rgba(253, 29, 29, 1) 50%,
    rgba(252, 176, 69, 1) 100%
  );
